<template>
  <div class="contact">
    <div class = "block content has-text-centered">
      <h1 class = "title">
        <span class="icon is-large has-text-primary-dark">
          <font-awesome-icon icon="leaf" />
        </span>
        Send us your contact to get emails about future meetings
        <span class="icon is-large has-text-primary-dark">
          <font-awesome-icon icon="leaf" />
        </span>
      </h1>
    </div>
  </div>
  <div class="container">
      <form ref = "form" @submit.prevent="sendEmail">
        <div class = "field">
          <label class = "label">Name</label>
          <input
            class = "input"
            type="text"
            v-model="name"
            name="name"
            placeholder="Your Name"
          >
        </div>

        <div class = "field">
          <label class = "label">Email</label>
          <div class = "control has-icons-left">
            <input
              class = "input"
              type="email"
              v-model="email"
              name="email"
              placeholder="Your Email"
              >
            <span class = "icon is-small is-left">
              <font-awesome-icon icon="envelope" />
            </span>
          </div>
        </div>

      <div class = "field">
        <div class = "control">
          <input class = "button has-text-primary-dark" type="submit" value = "Submit" style="font-weight: bold;">
        </div>
      </div>

      </form>
  </div>

  <div class="modal" v-bind:class = "{'is-active': showSuccess}">
    <div @click = "showSuccess = !showSuccess" class="modal-background"></div>

    <div class="modal-content">
      <div class="box has-text-centered has-text-primary-dark">
        <p><strong>You have been added to our email list. We look forward to meeting you!</strong></p>
      </div>
    </div>

    <!-- <button class="modal-close is-large" @click = "show = !show" aria-label="close"></button> -->
  </div>

  <div class="modal" v-bind:class = "{'is-active': showFailure}">
    <div @click = "showFailure = !showFailure" class="modal-background"></div>

    <div class="modal-content">
      <div class="box has-text-centered has-text-primary-dark">
        <p><strong>Your contact information failed to send. Please try again!</strong></p>
      </div>
    </div>

    <!-- <button class="modal-close is-large" @click = "show = !show" aria-label="close"></button> -->
  </div>
</template>

<script>
import emailjs from 'emailjs-com';

export default {
  name: 'ContactUs',
  data() {
    return {
      name: '',
      email: '',
      showSuccess: false,
      showFailure: false
    }
  },
  methods: {
    sendEmail() {
      emailjs.sendForm('service_bvp3ltp', 'template_ebo9f3a', this.$refs.form, '6BJf4qA5YFwnY7_qz')
        .then((result) => {
            console.log('SUCCESS!', result.text);
            this.showSuccess = !this.showSuccess;
        }, (error) => {
            console.log('FAILED...', error.text);
            this.showFailure = !this.showFailure;
        });
      // Reset form field
      this.name = ''
      this.email = ''
    }
  }
}
</script>
